element.style {
  background-color: #f5f5f5;
  padding-top: 85px;
  padding-bottom: 45px;
}

.gdlr-color-wrapper,
.gdlr-parallax-wrapper {
  overflow: hidden;
  background-position: center 0px;
  background-repeat: repeat;
  position: relative;
}
.gdlr-item-title-wrapper.pos-center {
  text-align: center;
}

.gdlr-item-title-wrapper.gdlr-item {
  margin-bottom: 40px;
}
.gdlr-item {
  margin: 0px 15px 20px;
  min-height: 1px;
}
.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  color: #ffffff;
}

.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  background-color: #72d5cd;
}
.gdlr-item-title-wrapper .gdlr-flex-prev {
  margin-right: 40px;
  padding: 15px 22px 15px 19px;
  overflow: hidden;
  border-radius: 50%;
}
.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  cursor: pointer;
}
.gdlr-item-title-wrapper .gdlr-item-title {
  color: #292929;
  border-color: #292929;
}

.gdlr-item-title-wrapper .gdlr-item-title {
  vertical-align: middle;
}
.gdlr-item-title-wrapper .gdlr-item-title {
  font-size: 25px;
  margin-bottom: 0px;
  line-height: 1;
  text-transform: uppercase;
  display: inline-block;
  font-weight: 600;
  letter-spacing: 2px;
}
.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  color: #ffffff;
}
.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  background-color: #72d5cd;
}
.gdlr-item-title-wrapper .gdlr-flex-next {
  margin-left: 40px;
  padding: 15px 19px 15px 22px;
  overflow: hidden;
  border-radius: 50%;
}
.gdlr-item-title-wrapper .gdlr-flex-prev,
.gdlr-item-title-wrapper .gdlr-flex-next {
  display: inline-block;
  vertical-align: middle;
  font-size: 18px;
  cursor: pointer;
}
.clear {
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  zoom: 1;
}
.gdlr-item-title-wrapper.pos-center .gdlr-item-title-divider {
  margin-left: auto;
  margin-right: auto;
}
.gdlr-item-title-wrapper .gdlr-item-title-divider {
  width: 30px;
  margin-top: 25px;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}
.gdlr-item-title-divider {
  border-color: #333333;
}
.gdlr-item {
  margin: 0px 15px 20px;
  min-height: 1px;
}
